<template>
    <div>
      <img src="../assets/ERP.png">
      <div class="form">
        <h2 class="title">FERP沙盘表格</h2>
        <el-form :model="loginForm" :rules="loginRules" ref="loginForm">
          <el-form-item prop="username">
            <el-input v-model="loginForm.username" placeholder="请输入用户名" type="text" required="required" class="input" ></el-input>
          </el-form-item>
          <el-form-item prop="password">
            <el-input v-model="loginForm.password" placeholder="请输入密码" type="password" required="required" class="input"></el-input>
          </el-form-item>
          <el-form-item>
          <el-button type="primary" @click="handleLogin">登陆</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
</template>

<script>
import { loginReq } from '@/apis/Login'
export default {
  name: 'login',
  data () {
    return {
      loginForm: {
        username: '',
        password: ''
      },
      loginRules: {
        username: [
          {
            required: true,
            message: '请输入用户名',
            trigger: 'blur'
          }
        ],
        password: [
          {
            required: true,
            message: '请输入密码',
            trigger: 'blur'
          }
        ]
      }
    }
  },
  methods: {
    handleLogin () {
      var that = this
      this.$refs.loginForm.validate(valid => {
        if (valid) {
          loginReq(this.loginForm.username, this.loginForm.password).then((res) => {
            if (res.data) {
              console.log('zhe1')
              if (res.data.success === 1) {
                console.log('zhe2')
                sessionStorage.setItem('accessToken', that.loginForm.username)
                console.log('登陆成功')
                this.$router.push({
                  name: 'index',
                  params: {
                    username: that.loginForm.username
                  }
                })
              } else {
                that.showError()
              }
            } else {
              that.showError()
            }
          })
        } else {
          console.log('用户名或密码错误')
        }
      })
    },
    showError () {
      console.log('用户名或密码错误')
      this.$notify.error({
        title: '错误',
        message: '用户名或密码错误',
        duration: 0
      })
    }
  }
}
</script>

<style>
  @import "../css/login.css";
  .input{
    width: 250px !important;
    margin-top: 10px;
  }
</style>



