import axios from 'axios'
var qs = require('qs')
export function loginReq (username, password) {
  return axios.post('http://localhost:8080/excel/login', qs.stringify({
    'username': username,
    'password': password
  }, {indices: false}),
  {
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded'
    },
    withCredentials: true})
}



